<template>
  <el-col>
    <el-button @click="styleJsonVisible=true" type="primary" style="margin-top: 4%;"></el-button><br/>
    <el-button @click="translateVisible=true" type="primary" style="margin-top: 4%;"></el-button><br/>
    <el-button @click="deviceColorVisible=true" type="primary" style="margin-top: 4%;"></el-button>
    <el-dialog :visible.sync="styleJsonVisible" title="">
      <p><a href="http://lbsyun.baidu.com/index.php?title=jspopular/guide/custom" target="_blank">http://lbsyun.baidu.com/index.php?title=jspopular/guide/custom</a></p>
      <el-form :rules="styleRules" :model="styleJson">
        <el-form-item prop="styleJson">
          <el-input type="textarea" placeholder="" v-model="styleJson.styleJson" autosize></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="confirm('styleJson')" :disabled="styleJson.disabled"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog :visible.sync="translateVisible" title="">
      <el-form :rules="translateRules" :model="translate">
        <el-form-item prop="translate">
          <el-input type="textarea" placeholder="" v-model="translate.translate" autosize></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="confirm('translate')" :disabled="translate.disabled"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog :visible.sync="deviceColorVisible" title="">
      <p></p>
      <el-form :rules="deviceColorRules" :model="deviceColor">
        <el-form-item prop="deviceColor">
          <el-input type="textarea" placeholder="" v-model="deviceColor.deviceColor" autosize></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="confirm('deviceColor')" :disabled="deviceColor.disabled"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </el-col>

</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      let checkJson = (rule, value, callback) => {
        try {
          JSON.parse(value.replace(/'/g, '"'))
          this[rule.field].disabled = false
          callback()
        } catch (err) {
          this[rule.field].disabled = true
          return callback(new Error(''))
        }
      }
      return {
        styleJsonVisible: false,
        styleJson: {
          styleJson: '',
          disabled: true
        },
        styleRules: {
          styleJson: [
            { validator: checkJson, trigger: 'change' },
            { validator: checkJson, trigger: 'blur' }
          ]
        },

        translateVisible: false,
        translate: {
          translate: '',
          disabled: true
        },
        translateRules: {
          translate: [
            { validator: checkJson, trigger: 'change' },
            { validator: checkJson, trigger: 'blur' }
          ]
        },
        deviceColorVisible: false,
        deviceColor: {
          deviceColor: '',
          disabled: true
        },
        deviceColorRules: {
          deviceColor: [
            { validator: checkJson, trigger: 'change' },
            { validator: checkJson, trigger: 'blur' }
          ]
        }
      }
    },
    mounted () {
      axios.get(`${this.$store.state.host}/style?fields=styleJson,translate,deviceColor`).then((res) => {
        this.styleJson.styleJson = JSON.stringify(res.data.styleJson, null, 2) || ''
        this.translate.translate = JSON.stringify(res.data.translate, null, 2) || ''
        this.deviceColor.deviceColor = JSON.stringify(res.data.deviceColor, null, 2) || ''
      })
    },
    methods: {
      confirm (item) {
        let _ = {}
        _[item] = JSON.parse(this[item][item].replace(/'/g, '"'))
        axios.post(`${this.$store.state.host}/style`, _).then(() => {
          this.$message.success('')
          this[item + 'Visible'] = false
        }).catch(() => {
          this.$message.error('')
        })
      }
    }
  }
</script>



