import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    devices: [],
    logInfo: [],
    runStatus: 'Stopped',
    isConnected: false,
    host: 'http://47.93.218.135',
    wshost: 'ws://47.93.218.135/ws'
  },
  mutations: {
    addDev (state, dev) {
      state.devices.push(dev)
    },
    addDevs (state, devs) {
      state.devices.push(...devs)
    },
    addLog (state, log) {
      state.logInfo.push(log)
    },
    startScan (state) {
      if (state.isConnected) {
        axios.get(`${state.host}/action/start`).then((res) => {
          state.runStatus = 'Running'
          Vue.prototype.$message.success('')
          return true
        }).catch((err) => {
          if (err.response.status === 400 && err.response.statusText === 'lack ip source') {
            Vue.prototype.$message.error('IP')
          } else if (err.response.status === 400 && err.response.statusText === 'lack ip source') {
            Vue.prototype.$message.error('Poc')
          } else {
            Vue.prototype.$message.error('')
          }
          return false
        })
      } else {
        Vue.prototype.$message.error('')
        return false
      }
    },
    pauseScan (state) {
      if (state.isConnected) {
        axios.get(`${state.host}/action/pause`).then(() => {
          state.runStatus = 'Paused'
          Vue.prototype.$message.success('')
          return true
        })
      } else {
        Vue.prototype.$message.error('')
        return false
      }
    },
    stopScan (state) {
      if (state.isConnected) {
        axios.get(`${state.host}/action/stop`).then(() => {
          state.runStatus = 'Stopped'
          Vue.prototype.$message.success('')
          return true
        })
      } else {
        Vue.prototype.$message.error('')
        return false
      }
    },
    finishScan (state) {
      state.runStatus = 'Finished'
      Vue.prototype.$message.success('')
    },
    connectSuccess (state) {
      state.isConnected = true
      Vue.prototype.$message.success('')
    },
    connectFail (state) {
      state.isConnected = false
      Vue.prototype.$message.error('')
    }
  }
})



