<template>
	<section>
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<!--<el-form-item>-->
					<!--<el-input v-model="filters.name" placeholder=""></el-input>-->
				<!--</el-form-item>-->
				<!--<el-form-item>-->
					<!--<el-button type="primary" v-on:click="getUsers"></el-button>-->
				<!--</el-form-item>-->
				<el-form-item>
					<el-button type="primary" @click="addFormVisible = true"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" :span="2">
			</el-table-column>
			<el-table-column type="index" :span="3">
			</el-table-column>
			<el-table-column prop="name" label="" :span="3" sortable>
			</el-table-column>
			<el-table-column prop="device_type" label="" :span="3" sortable>
			</el-table-column>
      <el-table-column prop="zoomeye_query" label="ZoomEye" :span="3">
      </el-table-column>
			<el-table-column label="" :span="3">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$name, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<!--<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>-->
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" ref="editForm" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off" :disabled="true"></el-input>
				</el-form-item>
        <el-form-item label="" prop="device_type">
          <el-input v-model="editForm.device_type" auto-complete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="ZoomEye" prop="zoomeye_query">
          <el-input v-model="editForm.zoomeye_query" auto-complete="off" :disabled="true"></el-input>
        </el-form-item>
				<el-form-item label="Poc" prop="content">
					<el-input type="textarea" v-model="editForm.content" autosize></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" ref="addForm" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="name">
          <el-input  v-model="addForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="device_type">
          <el-input  v-model="addForm.device_type" auto-complete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="ZoomEye" prop="zoomeye_query" :disabled="true">
          <el-input v-model="addForm.zoomeye_query" auto-complete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="Poc" prop="content">
          <el-input v-model="addForm.content" type="textarea" placeholder="" autosize></el-input>
        </el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
  import axios from 'axios'

  export default {
    data () {
      return {
        filters: {
          name: ''
        },
        users: [],
        total: 0,
        page: 1,
        listLoading: false,
        sels: [],  // 

        editFormVisible: false, // 
        editLoading: false,
        editFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        // 
        editForm: {
          id: 0,
          name: '',
          device_type: '',
          zoomeye_query: '',
          content: ''
        },

        addFormVisible: false,  // 
        addLoading: false,
        addFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        // 
        addForm: {
          id: 0,
          name: '',
          device_type: '',
          zoomeye_query: '',
          content: ''
        }
      }
    },
    methods: {
      handleCurrentChange (val) {
        this.page = val
        this.getUsers()
      },
      getUsers () {
        let para = {
          page: this.page,
          name: this.filters.name
        }
        this.listLoading = true
        axios.get(`${this.$store.state.host}/poc`, { params: para }).then((res) => {
          this.total = res.data.total
          this.users = res.data.pocs
          this.listLoading = false
        }).catch(() => {
          this.$message.error('Poc')
          this.listLoading = false
        })
      },
      handleDel (index, row) {
        this.$confirm('Poc?', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true
          axios.delete(`${this.$store.state.host}/poc/${row.name}`).then((res) => {
            this.listLoading = false
            this.$message.success('')
            this.getUsers()
          })
        }).catch((err) => {
          if (err.response.status === 404 && err.response.statusText === 'Poc File Not Found') {
            this.$message.error('Poc')
          } else {
            this.$message.error('')
          }
        })
      },
      // 
      handleEdit: function (index, row) {
        this.editFormVisible = true
        this.editForm = Object.assign({}, row)
      },
      // 
      editSubmit: function () {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.editLoading = true
              axios.put(`${this.$store.state.host}/poc/${this.editForm.name}`, this.editForm.content).then((res) => {
                this.editLoading = false
                this.$message.success('')
                this.$refs['editForm'].resetFields()
                this.editFormVisible = false
                this.getUsers()
              }).catch((err) => {
                if (err.response.status === 404 && err.response.statusText === 'Poc File Not Found') {
                  this.$message.error('Poc')
                } else {
                  this.$message.error('')
                }
              })
            })
          }
        })
      },
      // 
      addSubmit: function () {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              axios.post(`${this.$store.state.host}/poc/${this.addForm.name}`, this.addForm.content).then((res) => {
                this.addLoading = false
                this.$message.success('')
                this.$refs['addForm'].resetFields()
                this.addFormVisible = false
                this.getUsers()
              }).catch((err) => {
                if (err.response.status === 409 && err.response.statusText === 'Conflict') {
                  this.addLoading = false
                  this.$message.error('Poc')
                  this.$refs['addForm'].resetFields()
                  this.addFormVisible = false
                  this.getUsers()
                } else {
                  this.$message.error('Poc')
                }
              })
            })
          }
        })
      },
      selsChange: function (sels) {
        this.sels = sels
      }
      // 
//      batchRemove: function () {
//        let ids = this.sels.map(item => item.id).toString()
//        this.$confirm('', '', {
//          type: 'warning'
//        }).then(() => {
//          this.listLoading = true
//          // NProgress.start();
//          let para = { ids: ids }
//          batchRemoveUser(para).then((res) => {
//            this.listLoading = false
//            // NProgress.done();
//            this.$message({
//              message: '',
//              type: 'success'
//            })
//            this.getUsers()
//          })
//        }).catch(() => {
//        })
//      }
    },
    mounted () {
      this.getUsers()
    }
  }

</script>

<style scoped>

</style>



